/*
We use box shadows instead of borders since it makes the size and position
calculations much more well-behaved. This mixin allows us to define the themes
for all borders with minimal duplication. See the bottom of this file.
*/
.bp-table-container {
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.15); }

.bp-table-top-container {
  box-shadow: 0 1px 0 0 rgba(16, 22, 26, 0.15); }

.bp-table-menu {
  box-shadow: 1px 0 0 rgba(16, 22, 26, 0.15); }

.bp-table-header {
  box-shadow: 0 1px 0 rgba(16, 22, 26, 0.15); }

.bp-table-column-headers {
  box-shadow: 0 1px 0 0 rgba(16, 22, 26, 0.15); }

.bp-table-row-headers {
  box-shadow: 1px 0 0 rgba(16, 22, 26, 0.15), inset 0 1px 0 rgba(16, 22, 26, 0.15); }

.bp-table-cell {
  box-shadow: inset -1px 0 0 rgba(16, 22, 26, 0.15), inset 0 -1px 0 rgba(16, 22, 26, 0.15); }

.bp-table-horizontal-cell-divider {
  box-shadow: inset 0 1px 0 rgba(16, 22, 26, 0.15);
  height: 1px; }

.bp-table-column-headers .bp-table-header {
  box-shadow: inset -1px 0 0 rgba(16, 22, 26, 0.15); }
  .bp-table-column-headers .bp-table-header::before {
    right: 1px;
    bottom: 0; }
  .bp-table-column-headers .bp-table-header.bp-table-last-in-row {
    box-shadow: 1px 0 0 rgba(16, 22, 26, 0.15); }
    .bp-table-column-headers .bp-table-header.bp-table-last-in-row::before {
      right: 0; }

.bp-table-row-headers .bp-table-header {
  box-shadow: inset 0 -1px 0 rgba(16, 22, 26, 0.15); }
  .bp-table-row-headers .bp-table-header::before {
    right: 0;
    bottom: 1px; }
  .bp-table-row-headers .bp-table-header.bp-table-last-in-column {
    box-shadow: 0 1px 0 rgba(16, 22, 26, 0.15); }
    .bp-table-row-headers .bp-table-header.bp-table-last-in-column::before {
      bottom: 0; }

.bp-table-body .bp-table-last-in-row {
  box-shadow: inset 0 -1px 0 rgba(16, 22, 26, 0.15), 1px 0 0 rgba(16, 22, 26, 0.15); }

.bp-table-body .bp-table-last-in-column {
  box-shadow: inset -1px 0 0 rgba(16, 22, 26, 0.15), 0 1px 0 rgba(16, 22, 26, 0.15); }

.bp-table-body .bp-table-last-in-row.bp-table-last-in-column {
  box-shadow: 1px 0 0 rgba(16, 22, 26, 0.15), 0 1px 0 rgba(16, 22, 26, 0.15); }

.pt-dark .bp-table-container {
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.4); }

.pt-dark .bp-table-top-container {
  box-shadow: 0 1px 0 0 rgba(16, 22, 26, 0.4); }

