.bp-table-overlay-layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 20;
  pointer-events: none; }

.bp-table-overlay {
  position: absolute; }

.bp-table-region {
  border: 1px solid #8a9ba8;
  background-color: rgba(138, 155, 168, 0.1); }

.bp-table-selection-region {
  border: 1px solid #137cbd;
  background-color: rgba(19, 124, 189, 0.1); }
  .bp-table-column-headers .bp-table-selection-region {
    background-color: transparent; }
  .bp-table-row-headers .bp-table-selection-region {
    background-color: transparent; }

.bp-table-focus-region {
  border: 2px solid #137cbd; }

.bp-table-column-headers .bp-table-region {
  border-bottom: none; }

.bp-table-row-headers .bp-table-region {
  border-right: none; }

.bp-table-vertical-guide {
  top: 0;
  bottom: 0; }

.bp-table-horizontal-guide {
  right: 0;
  left: 0; }

.bp-table-container {
  transform: translateZ(0);
  display: flex;
  flex-direction: column;
  background-color: #f5f8fa;
  max-width: 100%;
  height: 100%;
  min-height: 60px;
  max-height: 100%;
  overflow: hidden; }
  .pt-dark .bp-table-container {
    background-color: #30404d; }
  .bp-table-container .pt-loading {
    color: transparent; }
    .bp-table-container .pt-loading .pt-skeleton {
      opacity: 0;
      height: 5px;
      animation: 300ms linear forwards skeleton-fade-in, 2000ms linear infinite glow;
      animation-delay: 0s, 300ms; }

.bp-table-top-container {
  display: flex;
  flex: 0 0 auto;
  min-height: 0%; }

.bp-table-bottom-container {
  display: flex;
  flex: 1 1 auto;
  height: 100%;
  min-height: 0%;
  color: #182026; }
  .pt-dark .bp-table-bottom-container {
    color: #f5f8fa; }

.bp-table-menu {
  flex: 0 0 auto;
  position: relative;
  z-index: 13;
  background-color: #f5f8fa; }
  .pt-dark .bp-table-menu {
    background-color: #30404d; }

.bp-table-column-headers {
  display: block;
  position: relative;
  z-index: 11;
  background-color: #f5f8fa;
  color: #182026; }
  .pt-dark .bp-table-column-headers {
    background-color: #30404d;
    color: #f5f8fa; }

.bp-table-row-headers {
  display: table;
  flex: 0 0 auto;
  position: relative;
  z-index: 12;
  background-color: #f5f8fa;
  color: #5c7080; }
  .pt-dark .bp-table-row-headers {
    background-color: #30404d;
    color: #bfccd6; }

.bp-table-body {
  flex: 1 1 100%;
  position: relative;
  z-index: 10;
  overflow: scroll; }
  .bp-table-body.bp-table-no-vertical-scroll {
    overflow-y: hidden; }
  .bp-table-body.bp-table-no-horizontal-scroll {
    overflow-x: hidden; }

.bp-table-body-scroll-client {
  transform: translateZ(0);
  position: relative;
  user-select: none; }

.bp-table-body-virtual-client {
  position: relative; }

.bp-table-cell-client {
  background: #ffffff; }
  .pt-dark .bp-table-cell-client {
    background: #293742; }

.bp-table-tbody {
  display: block;
  white-space: nowrap; }

.bp-table-cell {
  display: inline-block; }

.bp-table-no-layout {
  display: inline-block;
  position: absolute; }

.bp-table-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 100px;
  height: 100px;
  overflow: scroll; }
